//Color Variable
$color1: #EEEEEE;
$color2: #FFD369;
$color3: #393E46;
$color4: #1E2022;


.expense-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  margin: 1rem 0;
  background-color: $color3;
}

.expense-item__description {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: flex-end;
  flex-flow: column-reverse;
  justify-content: flex-start;
  flex: 1;
}

.expense-item h2 {
  color: $color1;
  font-size: 1rem;
  flex: 1;
  margin: 0 1rem;
}

.expense-item__price {
  font-size: 1rem;
  font-weight: bold;
  color: $color4;
  background-color: $color2;
  border: 1px solid $color4;
  padding: 0.5rem;
  border-radius: 12px;
}

@media (min-width: 580px) {
  .expense-item__description {
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    flex: 1;
  }

  .expense-item__description h2 {
    font-size: 1.25rem;
  }

  .expense-item__price {
    font-size: 1.25rem;
    padding: 0.5rem 1.5rem;
  }
}