//Color Variable
$color1: #EEEEEE;
$color2: #1E2022;

.expense-date {
  display: flex;
  flex-direction: column;
  width: 5.5rem;
  height: 5.5rem;
  border: 2px solid $color1;
  background-color: $color2;
  color: $color1;
  border-radius: 12px;
  align-items: center;
  justify-content: center;

  .expense-date__month {
    font-size: 0.75rem;
    font-weight: bold;
  }
  
  .expense-date__year {
    font-size: 0.75rem;
  }
  
  .expense-date__day {
    font-size: 1.5rem;
    font-weight: bold;
  }
}

